.footer_wrapper{
    background-color: rgba(250,240,150,0.86);
    display: flex;
    align-items: center;
    justify-content: center;
}
.footer{
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.lineFooter{
    height: 5px;
    background-color: rgba(255, 181, 16, 0.86);
    border-radius: 2px;
    width: 100%;
}
.footerText{
    text-align: center;
    letter-spacing: 5px;
    font-size: 15px;
    font-weight: 600;
}
